import React from "react";
import { setTimeout, clearTimeout } from "timers";

class Typewriter extends React.PureComponent {
    state = {
        text: ""
    }

    startTyping() {
        if (!this.props.text) {
            return;
        }

        this.timeout = setTimeout(() => {
            this.setState({
                text: `${this.state.text}${this.props.text.substring(this.textIndex, this.textIndex++)}`
            })

            this.textIndex = this.textIndex++
            this.startTyping();
        }, 100).bind(this);
    }

    stopTyping() {
        clearTimeout(this.timeout);
    }

    componentWillMount() {
        this.textIndex = 0;
    }

    componentDidMount() {
        this.startTyping()
    }

    componentWillUnmount() {
        this.stopTyping();
    }

    render() {
        return (
            <React.Fragment>
                {this.state.text}
            </React.Fragment>
        )
    }
}

export default Typewriter;